* {
	font-family: 'Roboto', sans-serif;
	margin: 0;
	padding: 0; }
nav {
	height: 50px;
	background-color: #212121;
	.menu {
		display: flex;
		justify-content: space-between;
		padding: 12px 70px;
		list-style-type: none;
		height: 50px;
		margin: 0;
		&_link {
            font-size: 15px;
            font-weight: 300;
            font-stretch: normal;
            font-style: normal;
            line-height: 15px;
            letter-spacing: normal;
            text-align: left;
            color: #fff;
            text-decoration: none; }
		&_item {
			border-right: 2px dotted black;
			padding-right: 35px; } } }
.subheader {
	height: 84px;
	background-color: rgba(0,0,0,0.6);
	&_logo {
		max-width: 100%;
		margin-top: 16px; }
	&_text {
		font-size: 13px;
		font-weight: 100;
		font-stretch: normal;
		font-style: normal;
		letter-spacing: normal;
		text-align: left;
		color: rgba(255, 255, 255, 0.5); }
	&_call {
		margin-top: 23px;
		font-size: 12px;
		font-weight: 300;
		font-stretch: normal;
		font-style: normal;
		letter-spacing: normal;
		text-align: right;
		color: #fff; }
	&_phone {
		display: block;
		text-decoration: none;
		font-size: 22px;
		font-weight: normal;
		font-stretch: normal;
		line-height: 16px;
		margin-top: 7px;
		font-style: normal;
		letter-spacing: normal;
		text-align: right;
		color: #fff; }
	&_btn {
		width: 172px;
		height: 46px;
		margin: 3px 0 2px 32px;
		padding: 18px 20px;
		box-shadow: inset 0px -3px 0 0 rgba(0, 0, 0, 0.12);
		border: solid 3px rgba(0, 0, 0, 0.07);
		background-image: linear-gradient(to top, #1eacc7, #4ce2ff);
		text-shadow: 0.7px 0.7px 0 rgba(0, 0, 0, 0.21);
		font-size: 10px;
		font-weight: normal;
		font-stretch: normal;
		font-style: normal;
		line-height: 10px;
		letter-spacing: normal;
		text-align: center;
		color: #fff; } }








